.container {
  display: flex;
  justify-content: space-around;
}

.biography {
  max-width: 40%;

  button {
    color: #fff;
    background-color: #6c63ff;
    border-radius: 2em;
    padding: 0.8em 2em;
    // border: 1px solid #6C63FF;
  }
}

.title {
  font-family: "Open Sans", sans-serif;
}

.description {
  font-family: "Poppins", sans-serif;
  text-align: justify;
}

.bioImg {
  width: 30em;
  max-width: 100%;
  height: auto;
}

@media screen and (max-width: 700px) {
  .container {
    display: block;
  }
  .biography {
    max-width: 100%;
    text-align: center;
  }
  .bioImg {
    display: none;
  }
}
