.container {
}

.title {
  text-align: center;
}

.cardContainer {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: auto;
}

.card {
  width: 20em;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  height: 28em;
  border-radius: 20px;
  margin: 1.2em;
  cursor: pointer;
}

.card:hover {
  transform: scale(1.05);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.12), 0 4px 8px rgba(0, 0, 0, 0.06);
}

.price {
  text-align: center;
  color: #7e76f9;
  font-size: 2em;
}

